@font-face {
  font-family: "Gilroy";
  src: url("../fonts/gilroyextrabold.woff2") format("woff2"), url("../fonts/gilroyextrabold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gilroy";
  src: url("../fonts/gilroylight.woff2") format("woff2"), url("../fonts/gilroylight.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansregular.woff2") format("woff2"), url("../fonts/opensansregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
body {
  margin: 0;
}
img {
  max-width: 100%;
}
a {
  text-decoration: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}
.about__wrapper {
  margin-bottom: 80px;
}
.about__container {
  display: flex;
  justify-content: space-between;
}
.about__section {
  padding-top: 50px;
}
.about__section::before {
  content: "";
  display: block;
  transform: translateY(-50px);
  width: 80px;
  height: 7px;
  background-color: #000;
}
.about__us {
  width: 600px;
  padding-right: 125px;
}
.about__contacts {
  width: 560px;
}
.about__title {
  margin: 0;
  margin-bottom: 40px;
  font-family: "Gilroy", sans-serif;
  font-size: 47px;
  line-height: 58px;
  font-weight: 800;
  letter-spacing: 0.2em;
}
.about__description {
  margin: 0;
  margin-bottom: 40px;
  line-height: 28px;
}
.about__list {
  margin: 0;
  margin-bottom: 50px;
  padding: 20px 0;
  list-style: none;
}
.about__item {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  font-family: "Gilroy";
  font-size: 16px;
  line-height: 28px;
  font-weight: 800;
}
.about__item:last-child {
  margin-bottom: 0;
}
.about__item::before {
  content: "";
  margin-right: 24px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  border: 2px solid #E5E5E5;
}
.about__map {
  display: block;
  margin-bottom: 74px;
}
.about__button {
  padding: 7px 18px 7px 24px;
}
.about__button--popup {
  padding: 7px 40px;
}
.button {
  display: inline-block;
  position: relative;
  font-family: 'Gilroy', sans-serif;
  font-size: 18px;
  line-height: 22px;
  font-weight: 800;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  text-align: center;
  color: #000;
  background-color: transparent;
  border: none;
  z-index: 1;
}
.button:active {
  color: rgba(0, 0, 0, 0.3);
}
.button::before {
  position: absolute;
  top: 50%;
  left: 0;
  z-index: -1;
  transform: translateY(-50%);
  content: '';
  width: 100%;
  height: 8px;
  background-color: #F0C52E;
}
.button:hover::before {
  height: 100%;
  transition: all 0.3s linear;
}
.container {
  width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}
.dropdown__list {
  position: absolute;
  z-index: 2;
  width: 1160px;
  min-height: 146px;
  overflow: hidden;
  display: none;
  margin: 0 -60px;
  padding: 18px 60px 30px;
  list-style: none;
  background-color: #F7E296;
}
.dropdown__item {
  display: flex;
  flex-direction: column;
  margin-right: 64px;
  padding: 12px 0;
}
.dropdown__link {
  margin-bottom: 16px;
  color: #000;
  cursor: pointer;
}
.dropdown__link:last-child {
  margin-bottom: 0;
}
.dropdown__link:hover {
  opacity: 0.6;
}
.dropdown__link:active {
  opacity: 0.3;
}
.features {
  min-height: 403px;
  background-image: linear-gradient(to bottom, transparent 0, transparent 115px, #E5E5E5 115px, #E5E5E5 115px);
}
.features__container {
  display: flex;
  padding-top: 15px;
  margin-bottom: 90px;
}
.features__wrapper {
  margin-bottom: 90px;
}
.features__controls {
  min-width: 284px;
  min-height: 320px;
  padding-top: 80px;
  border-right: 7px solid #000;
}
.features__control {
  display: block;
  width: 160px;
  padding: 9px 20px;
  margin-bottom: 21px;
}
.features__control:last-child {
  margin-bottom: 0;
}
.features__control:active {
  color: #F7E296;
}
.features__control:active::before {
  width: 277px;
  height: 100%;
  background-color: #000;
}
.features__control--active {
  color: #F7E296;
}
.features__control--active::before {
  width: 277px;
  height: 100%;
  background-color: #000;
}
.features__list {
  margin: 0;
  padding: 0;
  padding-top: 71px;
  padding-left: 116px;
  list-style: none;
}
.features__item {
  position: relative;
  display: none;
  width: 758px;
  background-position: top right;
  background-repeat: no-repeat;
}
.features__item::after {
  content: "";
  position: absolute;
  background-repeat: no-repeat;
}
.features__item--active {
  display: block;
}
.features__item--delivery {
  padding-right: 258px;
}
.features__item--delivery::after {
  top: 0;
  right: 0;
  width: 136px;
  height: 164px;
  background-image: url(../img/delivery-icon.svg);
  background-size: cover;
}
.features__item--warranty {
  position: relative;
  padding-right: 188px;
}
.features__item--warranty::after {
  top: -18px;
  right: -18px;
  width: 171px;
  height: 195px;
  background-image: url(../img/warranty-icon.svg);
  background-size: cover;
}
.features__item--loan {
  position: relative;
  padding-right: 188px;
}
.features__item--loan::after {
  top: -23px;
  right: 6px;
  width: 156px;
  height: 188px;
  background-image: url(../img/loan-icon.svg);
  background-size: cover;
}
.features__title {
  margin: 0;
  margin-bottom: 40px;
  font-family: "Gilroy", sans-serif;
  font-size: 47px;
  line-height: 58px;
  font-weight: 800;
  letter-spacing: 0.2em;
}
.features__description {
  margin: 0;
  line-height: 30px;
  letter-spacing: 0.1em;
}
.page__footer {
  background-color: #363636;
}
.footer__container {
  padding-top: 46px;
  padding-bottom: 64px;
}
.footer__top {
  display: flex;
  margin-bottom: 36px;
}
.footer__top .user-nav__list {
  width: auto;
}
.footer__logo {
  padding-top: 17px;
  margin-right: auto;
}
.footer__logo .logo__link:hover {
  opacity: 0.6;
}
.footer__logo .logo__link:active {
  opacity: 0.3;
}
.footer__middle {
  display: flex;
  margin-bottom: 48px;
}
.footer__middle .site-nav__item:last-child {
  margin-right: 0;
}
.footer__address {
  margin: 0;
  margin-right: 156px;
  color: #fff;
}
.footer__tel {
  margin-left: auto;
  color: #fff;
}
.footer__bottom {
  display: flex;
  align-items: center;
}
.footer__bottom::before {
  content: "";
  display: block;
  margin-bottom: 4px;
  margin-right: 432px;
  width: 80px;
  height: 7px;
  background-color: #FFC600;
}
.footer__copyright {
  margin-left: auto;
}
.footer__copyright:hover {
  opacity: 0.6;
}
.footer__copyright:active {
  opacity: 0.3;
}
.footer__copyright-logo {
  display: block;
  width: 115px;
  height: 33px;
}
.header__logo {
  position: absolute;
  top: -20px;
  left: 60px;
}
.logo__icon--footer path {
  fill: #F0C52E;
}
.main-nav__top {
  display: flex;
  margin-bottom: 36px;
}
.main-nav__bottom {
  margin-bottom: 35px;
}
.page {
  font-family: "Open Sans", sans-serif;
  font-size: 15px;
  line-height: 20px;
  font-weight: 300;
  color: #000;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}
.page__header {
  position: relative;
  min-height: 680px;
  margin-top: 40px;
  margin-bottom: 60px;
  padding: 44px 60px 20px;
  background-image: linear-gradient(to bottom, #F7E296 0%, #F7E296 300px, #fff 300px, #fff 100%);
}
.partners__wrapper {
  margin-bottom: 90px;
}
.partners__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}
.partners__item {
  width: 260px;
  height: 100px;
  margin-right: 40px;
  background-size: cover;
  background-repeat: no-repeat;
}
.partners__item:last-child {
  margin-right: 0;
}
.partners__item--DJI:hover .partners__logo {
  background-image: url(../img/logo-DJI-color.png);
}
.partners__item--SP:hover .partners__logo {
  background-image: url(../img/logo-SP-color.png);
}
.partners__item--GoPro:hover .partners__logo {
  background-image: url(../img/logo-GoPro-color.png);
}
.partners__item--Vive:hover .partners__logo {
  background-image: url(../img/logo-Vive-color.png);
}
.popular__wrapper {
  margin-bottom: 49px;
}
.popular__list {
  display: flex;
  margin: 0;
  padding: 20px 0;
  list-style: none;
}
.popular__item {
  width: 160px;
  margin-right: 40px;
}
.popular__item:last-child {
  margin-right: 0;
}
.popular__link:hover .popular__image {
  background-color: #F0C52E;
  transition: all 0.3s linear;
}
.popular__link:active .popular__icon,
.popular__link:active .popular__title {
  opacity: 0.3;
}
.popular__image {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 39px;
  width: 160px;
  height: 160px;
  background-color: #F7E296;
}
.popular__icon {
  object-fit: cover;
}
.popular__icon--vr {
  width: 97px;
  height: 55px;
}
.popular__icon--selfie {
  width: 86px;
  height: 117px;
}
.popular__icon--camera {
  width: 71px;
  height: 87px;
}
.popular__icon--fitnes {
  width: 107px;
  height: 65px;
}
.popular__icon--watches {
  width: 56px;
  height: 98px;
}
.popular__icon--quadro {
  width: 132px;
  height: 69px;
}
.popular__title {
  margin: 0;
  font-family: "Gilroy", sans-serif;
  font-size: 18px;
  line-height: 22px;
  font-weight: 800;
  letter-spacing: 0.05em;
  color: #000;
}
.popular__title--fitnes {
  letter-spacing: 0;
}
.search {
  width: 540px;
}
.search__form {
  position: relative;
  width: 440px;
}
.search__form:focus-within .search__button {
  display: block;
}
.search__form:focus-within .search__input {
  outline: none;
  box-shadow: inset 0 -2px #000;
}
.search__input {
  width: 356px;
  padding: 0 38px 14px;
  border: none;
  background-color: transparent;
  background-image: url("../img/search-icon.svg");
  background-size: 13px 13px;
  background-repeat: no-repeat;
  background-position: 1px 4px;
  font: inherit;
  cursor: pointer;
}
.search__input::-webkit-search-cancel-button {
  appearance: none;
}
.search__input::placeholder {
  opacity: 0.3;
}
.search__input:hover::placeholder {
  opacity: 0.6;
}
.search__button {
  display: none;
  position: absolute;
  top: -20px;
  right: 4px;
  padding: 15px 17px;
  border: 2px inset #000;
  background-color: transparent;
  font: inherit;
  text-transform: uppercase;
  cursor: pointer;
}
.search__button:hover {
  background-color: #000;
  color: #fff;
  outline: none;
}
.search__button:active {
  background-color: #000;
  color: rgba(255, 255, 255, 0.3);
}
.site-nav--footer .site-nav__link {
  color: #fff;
}
.site-nav__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}
.site-nav__item {
  margin-right: 35px;
}
.site-nav__item--catalog {
  width: 540px;
  margin-right: 0;
}
.site-nav__item--catalog:focus-within > .dropdown__list,
.site-nav__item--catalog:hover > .dropdown__list {
  display: flex;
}
.site-nav__item--contacts {
  margin-left: auto;
  margin-right: -4px;
}
.site-nav__link {
  font-family: "Gilroy", sans-serif;
  font-size: 18px;
  line-height: 22px;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  color: #000;
  box-sizing: content-box;
}
.site-nav__link--catalog {
  display: flex;
  align-items: center;
}
.site-nav__link--catalog::after {
  display: block;
  margin-left: 13px;
  content: "";
  width: 22px;
  height: 22px;
  background-image: url("../img/icon-plus.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.site-nav__link:hover {
  opacity: 0.6;
}
.site-nav__link:active {
  opacity: 0.3;
}
.slide--01 .slide__title::after {
  right: -44px;
}
.slide__image {
  width: 540px;
}
.slide__picture {
  object-fit: cover;
}
.slide__picture--01 {
  width: 526px;
  height: 334px;
  margin-top: 83px;
  margin-left: -30px;
}
.slide__picture--02 {
  width: 345px;
  height: 485px;
  margin-top: 25px;
  margin-left: 56px;
}
.slide__picture--03 {
  width: 384px;
  height: 486px;
  margin-left: 29px;
}
.slide__info {
  width: 500px;
  margin-left: auto;
}
.slide__title {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0;
  margin-bottom: 26px;
  font-family: "Gilroy", sans-serif;
  font-size: 47px;
  line-height: 58px;
  font-weight: 800;
  z-index: 1;
}
.slide__title::before {
  display: block;
  margin-top: 34px;
  margin-bottom: 52px;
  content: "";
  width: 100px;
  height: 7px;
  background-color: #fff;
}
.slide__title::after {
  content: attr(data-number);
  position: absolute;
  top: -16px;
  right: -14px;
  width: 212px;
  height: 170px;
  font-family: "Gilroy", sans-serif;
  font-size: 179px;
  line-height: 220px;
  color: #fff;
  z-index: -1;
}
.slide__description {
  margin: 0;
  margin-bottom: 53px;
}
.slide__button {
  padding: 7px 44px;
  margin-bottom: 38px;
  background-color: transparent;
}
.slide__button:active {
  color: rgba(0, 0, 0, 0.3);
}
.slide__details {
  display: flex;
  font-family: "Gilroy", sans-serif;
  font-weight: 300;
}
.slide__details dt {
  margin-bottom: 15px;
  font-size: 36px;
  line-height: 42px;
  letter-spacing: 0.1em;
}
.slide__details dd {
  margin: 0;
  font-size: 13px;
  line-height: 15px;
  letter-spacing: 0.1em;
}
.slide__details-group {
  margin-right: 62px;
}
.slide__details-group:last-child {
  margin-right: 0;
}
.slider-controls__list {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}
.slider-controls__item {
  display: block;
  margin-right: 18px;
}
.slider-controls__button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  width: 12px;
  height: 12px;
  border-radius: 50px;
  border: 1px solid #000;
  background-color: transparent;
  cursor: pointer;
}
.slider-controls__button--active::before {
  display: block;
  content: "";
  width: 6px;
  height: 6px;
  border-radius: 50%;
  border: 1px solid #000;
}
.slider {
  position: relative;
}
.slider__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.slider__item {
  display: none;
}
.slider__item--show {
  display: flex;
}
.slider__controls {
  position: absolute;
  top: 340px;
  right: 0;
  z-index: 1;
  margin-right: 10px;
}
.social__list {
  display: flex;
  justify-self: center;
  margin: 0;
  padding: 0;
  list-style: none;
}
.social__item {
  margin-right: 20px;
}
.social__item:last-child {
  margin-right: 0;
}
.social__link {
  opacity: 0.3;
}
.social__link:hover {
  opacity: 0.6;
}
.social__link:active {
  opacity: 0.1;
}
.user-nav__list {
  display: flex;
  flex-wrap: wrap;
  width: 500px;
  margin: 0;
  padding: 0;
  list-style: none;
}
.user-nav__item {
  margin-right: 43px;
  padding-bottom: 12px;
}
.user-nav__item:last-child {
  margin-right: 0;
}
.user-nav__item:nth-child(2) {
  margin-left: auto;
}
.user-nav__item--footer:nth-child(2) {
  margin-left: 0;
}
.user-nav__link {
  display: flex;
  align-items: center;
  color: #000;
}
.user-nav__link--footer {
  color: rgba(255, 255, 255, 0.7);
}
.user-nav__link--login .user-nav__icon {
  margin-right: 11px;
}
.user-nav__link--compare .user-nav__icon,
.user-nav__link--cart .user-nav__icon {
  margin-right: 12px;
}
.user-nav__link:hover {
  opacity: 0.6;
}
.user-nav__link:active {
  opacity: 0.3;
}
.user-nav__icon--footer path {
  fill: #F0C52E;
}
